import * as React from 'react';
import '../../style/Card.css'

class Card extends React.Component {
    constructor(props) {
        super();
        this.state = {
            index: props.index,
            name: props.name,
            content: props.content,
            techTags: props.techTags,
            libTags: props.libTags,
            imgUrl: props.imgUrl,
            readMeUrl: props.readMeUrl
        };
    }

    render() {
        console.log(this.state.readMeUrl)
        return (
            <div id={`card-${this.state.index}`} className="card">
                <div className="card-image">
                    <img src={this.state.imgUrl} alt="img" />
                </div>
                <div className="card-content">
                    <span className="card-title">{this.state.name}</span>
                    <p className="location">{this.state.content}</p>
                    <p className="tags">
                    <label>Tags: </label>
                    {this.state.techTags.map(t=>
                        <div className="chip"> {t} </div>)
                    }
                    <br/>
                    {this.state.libTags.map(t=>
                        <div className="chip"> {t} </div>)
                    }
                    </p>
                    <a href={this.state.readMeUrl} class="read-me" >READ ME</a>
                </div>
            </div>
        );
    }
}
export default Card;